/**
 * @section     Extends
 * @subsection  Button
 */

.button {
  background-color: $primary-color;
  border-radius: 3px;
  color: white;
  display: inline-block;
  font-weight: bold;
  height: 50px;
  line-height: 50px;
  margin: 0 10px 10px 0;
  padding: 0 35px;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  white-space: nowrap;

  &:active {
    top: 1px;
  }

  &:active,
  &:focus,
  &:hover {
    background-color: shade($primary-color, 15%);
    color: white;
    outline: none;
    text-decoration: none;
  }

  &.secondary {
    background-color: $secondary-color;
    color: white;

    &:active,
    &:focus,
    &:hover {
      background-color: shade($secondary-color, 15%);
    }
  }
}

/**
* @section     Extends
* @subsection  Ellipsis
*/

.ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
