/**
 * @section Images
 * 
 * Defines the default styling of images
 *
 * @package     WordPress
 * @subpackage  TrendPress
 * @author      Trendwerk
 * @license     GPL v2
 */

/**
 * @section     Images
 * @subsection  Alignment
 */

.alignleft {
  float: left;
  margin: 10px 20px;
}

.alignright {
  float: right;
  margin: 10px 0 10px 20px;
}

.aligncenter {
  display: block;
  margin: 0 auto;
}

/**
 * @section     Images
 * @subsection  Captions
 */

.wp-caption {
  background-color: $light-background;
  margin-bottom: $line-height;
  max-width: 100%;
  text-align: center;

  img { 
    box-sizing: border-box;
    padding: 5px;
  }
    
  .wp-caption-text {
    display: inline-block;
    font-size: 12px;
    line-height: 20px;
    margin: 0 10px 10px;
  }
}

/**
 * @section     Images
 * @subsection  Gallery
 */

.gallery {
  clear: both;
  float: left;
  margin-bottom: $line-height;
  width: 100%;

  .gallery-item {
    float: left;
    margin: 0 4px 4px 0;
    overflow: hidden;
    padding: 0;
    position: relative; 

    img {
      display: block;
      float: left;
      height: auto;
      width: 100%;
    }
    
    &:hover .gallery-caption {
      opacity: 1;
    }
  }

  .gallery-caption {
    @include animate-this(opacity, 400ms, ease-in-out);
    background-color: black;
    box-sizing: border-box;
    color: white;
    font-size: 12px;
    line-height: 1.3;
    margin: 0;
    max-height: 50%;
    opacity: 0;
    padding: 2px 8px;
    position: absolute;
    bottom: 0;
    left: 0;
    text-align: left;
    width: 100%;

    &:before {
      box-shadow: 0 -10px 15px black inset;
      content: '';
      height: 100%;
      left: 0;
      min-height: 49px;
      position: absolute;
      top: 0;
      width: 100%;
    }

  }

}

.gallery-columns-1 .gallery-item {
  max-width: 100%;
  width: auto;
}

.gallery-columns-2 .gallery-item {
  max-width: 48%;
  max-width: calc(50% - 14px);
  width: auto;
}

.gallery-columns-3 .gallery-item {
  max-width: 32%;
  max-width: calc(33.3% - 11px);
  width: auto;
}

.gallery-columns-4 .gallery-item {
  max-width: 23%;
  max-width: calc(25% - 9px);
  width: auto;
}

.gallery-columns-5 .gallery-item {
  max-width: 19%;
  max-width: calc(20% - 8px);
  width: auto;
}

.gallery-columns-6 .gallery-item {
  max-width: 15%;
  max-width: calc(16.7% - 7px);
  width: auto;
}

.gallery-columns-7 .gallery-item {
  max-width: 13%;
  max-width: calc(14.28% - 7px);
  width: auto;
}

.gallery-columns-8 .gallery-item {
  max-width: 11%;
  max-width: calc(12.5% - 6px);
  width: auto;
}

.gallery-columns-9 .gallery-item {
  max-width: 9%;
  max-width: calc(11.1% - 6px);
  width: auto;
}

.gallery-columns-1 .gallery-item:nth-of-type(1n),
.gallery-columns-2 .gallery-item:nth-of-type(2n),
.gallery-columns-3 .gallery-item:nth-of-type(3n),
.gallery-columns-4 .gallery-item:nth-of-type(4n),
.gallery-columns-5 .gallery-item:nth-of-type(5n),
.gallery-columns-6 .gallery-item:nth-of-type(6n),
.gallery-columns-7 .gallery-item:nth-of-type(7n),
.gallery-columns-8 .gallery-item:nth-of-type(8n),
.gallery-columns-9 .gallery-item:nth-of-type(9n) {
  margin-right: 0;
}

.gallery-columns-1 .gallery-item:nth-of-type(1n),
.gallery-columns-2 .gallery-item:nth-of-type(2n - 1),
.gallery-columns-3 .gallery-item:nth-of-type(3n - 2),
.gallery-columns-4 .gallery-item:nth-of-type(4n - 3),
.gallery-columns-5 .gallery-item:nth-of-type(5n - 4),
.gallery-columns-6 .gallery-item:nth-of-type(6n - 5),
.gallery-columns-7 .gallery-item:nth-of-type(7n - 6),
.gallery-columns-8 .gallery-item:nth-of-type(8n - 7),
.gallery-columns-9 .gallery-item:nth-of-type(9n - 8) {
  clear: left;
  margin-left: 0;
}

#tinymce {

  .gallery-columns-1 .gallery-item:nth-of-type(1n),
  .gallery-columns-2 .gallery-item:nth-of-type(2n - 1),
  .gallery-columns-3 .gallery-item:nth-of-type(3n - 2),
  .gallery-columns-4 .gallery-item:nth-of-type(4n - 3),
  .gallery-columns-5 .gallery-item:nth-of-type(5n - 4),
  .gallery-columns-6 .gallery-item:nth-of-type(6n - 5),
  .gallery-columns-7 .gallery-item:nth-of-type(7n - 6),
  .gallery-columns-8 .gallery-item:nth-of-type(8n - 7),
  .gallery-columns-9 .gallery-item:nth-of-type(9n - 8) {
    margin-left: 12px;
  }

}

.gallery-columns-7 .gallery-caption,
.gallery-columns-8 .gallery-caption,
.gallery-columns-9 .gallery-caption {
  display: none;
}

/**
 * @section     Images
 * @subsection  Set max width for $content_width (http://codex.wordpress.org/Content_Width)
 */

.aligncenter,
.alignright,
.alignleft,
.alignnone {
  img {
    max-width: 100%;
    height: auto;
  }
}

.size-thumbnail,
.size-medium,
.size-large,
.size-auto,
.alignnone, 
.size-full {
  max-width: 100%;
  height: auto;
}

/**
 * @section     Images
 * @subsection  Video-container for better responsive videos
 */

.video-container {
  height: 0;
  margin-bottom: 30px;
  padding-top: 30px;
  padding-bottom: 56.25%;
  position: relative;

  iframe,
  object,
  embed,
  video {
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
  }
}
